import React, {useEffect} from 'react';

/**
 * Custom hook para controle da edio de entidades filhos de uma entidade principal.
 * Por exemplo, itens, etapas e entregveis de uma Ordem de Servio
 *
 * @param funcaoAdicionarCallback Funo que efetivamente realiza a incluso
 * do item no array dentro da entidade principal
 * @param funcaoAtualizarCallback Funo que efetivamente realiza a atualizao
 * do item no array dentro da entidade principal
 * @param funcaoRemoverCallback Funo que efetivamente realiza a remoo
 * do item no array dentro da entidade principal
 * @param refInputFoco Campo que receber o foco aps a chamada de "editar"
 * @param refBotaoFoco Boto que receber o foco aps concluir a edio
 */
export const useControleEdicaoEntidadesFilhos = <T extends {}>(
    funcaoAdicionarCallback: (instancia: T) => void,
    funcaoAtualizarCallback: (instancia: T, indice: number) => void,
    funcaoRemoverCallback: (indice: number) => void,
    refInputFoco: React.RefObject<HTMLInputElement>,
    refBotaoFoco: React.RefObject<HTMLInputElement>,
) => {
    const [instancia, setInstancia] = React.useState<any>(null);
    //se estiver editando, essa varivel guarda a posio no array
    const [instanciaIndice, setInstanciaIndice] = React.useState<number>(-1);
    const [mostraForm, setMostraForm] = React.useState(false);

    //quando mudar o valor de mostraForm, se for para TRUE, foca no campo de foco
    //passado. Se for FALSE, foca no boto adicionar
    useEffect(() => {
        if (mostraForm && refInputFoco.current != null) {
            refInputFoco.current.focus();
        } else if (!mostraForm && refBotaoFoco.current != null) {
            refBotaoFoco.current.focus();
        }
    }, [mostraForm]);

    /**
     * Abre formulrio para iniciar edio de uma nova instncia
     *
     * @param instanciaEmBranco Instncia com propriedades vazias a ser editada
     */
    const criar = (instanciaAEditar: Partial<T>) => {
        editar(instanciaAEditar, -1);
    };

    /**
     * Abre formulrio para iniciar edio de instncia existente ou uma nova
     *
     * @param instanciaAEditar Instncia a ser editada
     * @param indice Se editando item existente, esta  sua posio no array da
     * respectiva propriedade dentro da entidade principal
     */
    const editar = (instanciaAEditar: Partial<T>, indice: number) => {
        if (instancia != null) {
            console.log(
                instancia == instanciaAEditar,
                JSON.stringify(instancia) == JSON.stringify(instanciaAEditar),
                Object.is(instancia, instanciaAEditar),
                JSON.stringify(instancia),
                JSON.stringify(instanciaAEditar),
            );
            fecharForm();
        } else {
            if (indice) setInstanciaIndice(indice);
            setInstancia(instanciaAEditar);
            setMostraForm(true);
        }
    };

    /**
     * Abre formulrio para iniciar edio de instncia existente ou uma nova
     *
     * @param instancia Instncia a ser editada
     * @param indice Se editando item existente, esta  sua posio no array da
     * respectiva propriedade dentro da entidade principal
     */
    const confirmar = (instancia: T) => {
        if (instanciaIndice > -1) {
            funcaoAtualizarCallback(instancia, instanciaIndice);
        } else {
            funcaoAdicionarCallback(instancia);
        }
        fecharForm();
    };

    /**
     * Fecha o formulrio de edio
     */
    const fecharForm = () => {
        setInstanciaIndice(-1);
        setInstancia(null);
        setMostraForm(false);
    };
    /**
     * Faz o controle necessrio para permitir a remoo de um item no array de subentidades e,
     * atendendo aos requisitos, chama a {funcaoRemoverCallback}
     *
     * @param indice Indice do item no array de subentidades que ser removido
     */
    const remover = (indice: number) => {
        //Se estiver rolando uma edio, fecha o form antes de remover
        if (instancia != null) {
            fecharForm();
        } else {
            funcaoRemoverCallback(indice);
        }
    };

    return {
        criar,
        editar,
        confirmar,
        fecharForm,
        remover,
        instancia,
        mostraForm,
    };
};
