const nomeEntidades: {[nomeClasseModel: string]: string} = {
    Contrato: 'Contrato',
    Fornecedor: 'Fornecedor',
    OrdemServico: 'Ordem de Servio',
    OrdemServicoFull: 'Ordem de Servio',
    EntregavelOrdemServico: 'Entregvel',
    EntregavelTipoOrdemServico: 'Entregvel',
    EtapaOrdemServico: 'Etapa',
    IndicadorNiveisServicoContrato: 'Indicador de Nvel de Servio',
    IndicadorOrdemServico: 'Indicador de Nvel de Servio',
    ItemOrdemServico: 'Item',
    MetricaContrato: 'Mtrica do Contrato',
    PapelContrato: 'Papel do Contrato',
    Papel: 'Papel',
    SancaoIndicadorNiveisServicoContrato: 'Sano de Indicador de Nvel de Servio do Contrato',
    TipoOrdemServicoContrato: 'Tipo de Ordem de Servio do Contrato',
};
export function formataMensagemErroLoopback(error: any) {
    if (error.code == 'VALIDATION_FAILED') {
        return 'Os dados enviados ao servidor no so vlidos: '.concat(
            error.details
                .map((d: {message: string; code: string; path: string}) => {
                    return d.message.replace(/should have required property/g, 'atributo no informado');
                })
                .join(', '),
        );
    } else if (error.code == 'ENTITY_NOT_FOUND') {
        const regex = /Entity not found:(.*) with id (\d+)/gm;
        const partes = regex.exec(error.message);
        console.log(partes);

        if (partes != null && partes?.length > 2) {
            return `O servidor no encontrou  ${
                nomeEntidades[partes[1].trim()] ? nomeEntidades[partes[1].trim()] : partes[1]
            } com o ID ${partes[2]}`;
        }
    } else if (error.statusCode == 500) {
        return 'Falha no servidor da aplicao. Entre em contato com a rea responsvel pelo suporte ao usurio';
    }
    return error.message;
}

export function formataMensagemErro(error: Error) {
    if (error.message.toLocaleLowerCase() == 'network error') {
        return 'Falha de comunicao entre a sua estao de trabalho e o servidor. Verifique sua conexo ou entre em contato com a rea responsvel pelo suporte ao usurio';
    } else if (error.message.toLocaleLowerCase() == 'internal server error') {
        return 'Falha no servidor da aplicao. Entre em contato com a rea responsvel pelo suporte ao usurio';
    }
    return error.message;
}
